import {
  CheckOutlined,
  CloseOutlined,
  DeleteOutlined,
  EditOutlined,
  PlusOutlined,
} from "@ant-design/icons";
import { Button, Card, Col, Row, Tabs, Tag } from "antd";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";
import {
  CDetailUserGroupData,
  CDetailUserGroupTable,
  CError,
  CLoadingSpinner,
  CModal,
  CPageTitle,
  CSelect,
  CSwitch,
  CTitle,
} from "@components";
import CApi from "@api";
import { FGetAccess, FRemove, FSubmit } from "@function";

const DetailAccessMenu = () => {
  const router = useRouter();
  const { id } = router.query;
  const [listMenu, setListMenu] = useState([]);
  const [listMenuUser, setListMenuUser] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isLoadingTable, setIsLoadingTable] = useState(false);
  const [isHaveData, setIsHaveData] = useState(true);
  const [isSubmit, setIsSubmit] = useState(false);
  const [isModal, setIsModal] = useState(false);
  const [errors, setErrors] = useState({});
  const [detail, setDetail] = useState({
    id: "",
    code: "",
    name: "",
  });
  const [accessList, setAccessList] = useState({
    viewAny: true,
    view: true,
    create: true,
    update: true,
    delete: true,
  });

  useEffect(() => {
    FGetAccess(router, setAccessList, setIsLoading);
  }, []);
  const [forms, setForms] = useState({
    id: "",
    user_group_id: id,
    menu_id: "",
    viewAny: true,
    view: true,
    create: true,
    update: true,
    delete: false,
  });
  useEffect(() => {
    getData();
    getListMenu();
    getListMenuUser();
  }, []);
  const getData = () => {
    setIsLoading(true);
    CApi({
      method: "GET",
      url: "api/setup/access-menu/" + id,
    })
      .then((res) => {
        setDetail(res.data);
      })
      .catch((err) => {
        if (err.response.status == 404) {
          setIsHaveData(false);
          setErrors(err.response);
        }
      })
      .finally(() => {
        setIsLoading(false);
      });
  };
  const getListMenu = () => {
    CApi({
      method: "GET",
      url: "api/list-menus",
    })
      .then((res) => {
        setListMenu(res.data);
      })
      .catch((_err) => {});
  };

  const getListMenuUser = () => {
    setIsLoadingTable(true);
    CApi({
      method: "GET",
      url: "api/setup/access-menu-permision/" + id,
    })
      .then((res) => {
        setListMenuUser(res.data);
      })
      .catch((_err) => {})
      .finally(() => {
        setIsLoadingTable(false);
      });
  };

  const handleEdit = (e) => {
    setIsModal(!isModal);
    setForms({
      id: e.id,
      user_group_id: id,
      menu_id: e.menu_id,
      viewAny: e.viewAny,
      view: e.view,
      create: e.create,
      update: e.update,
      delete: e.delete,
    });
  };
  const onMenu = (value) => {
    setForms({
      ...forms,
      menu_id: value,
    });
  };

  const onCancel = () => {
    setIsModal(!isModal);
    setForms({
      id: "",
      user_group_id: id,
      menu_id: "",
      viewAny: true,
      view: true,
      create: true,
      update: true,
      delete: false,
    });
  };

  const columns = [
    {
      title: "Menu",
      key: "menu",
      dataIndex: "menu",
      align: "center",
      ellipsis: true,
    },
    {
      title: "View Any",
      key: "viewAny",
      dataIndex: "viewAny",
      align: "center",
      width: 100,
      render: (item) =>
        item ? (
          <Tag color="#5cb85c">
            <CheckOutlined />
          </Tag>
        ) : (
          <Tag color="#cd201f">
            <CloseOutlined />
          </Tag>
        ),
    },
    {
      title: "View",
      key: "view",
      dataIndex: "view",
      align: "center",
      width: 100,
      render: (item) =>
        item ? (
          <Tag color="#5cb85c">
            <CheckOutlined />
          </Tag>
        ) : (
          <Tag color="#cd201f">
            <CloseOutlined />
          </Tag>
        ),
    },
    {
      title: "Create",
      key: "create",
      dataIndex: "create",
      align: "center",
      width: 100,
      render: (item) =>
        item ? (
          <Tag color="#5cb85c">
            <CheckOutlined />
          </Tag>
        ) : (
          <Tag color="#cd201f">
            <CloseOutlined />
          </Tag>
        ),
    },
    {
      title: "Update",
      key: "update",
      dataIndex: "update",
      align: "center",
      width: 100,
      render: (item) =>
        item ? (
          <Tag color="#5cb85c">
            <CheckOutlined />
          </Tag>
        ) : (
          <Tag color="#cd201f">
            <CloseOutlined />
          </Tag>
        ),
    },
    {
      title: "Delete",
      key: "delete",
      dataIndex: "delete",
      align: "center",
      width: 100,
      render: (item) =>
        item ? (
          <Tag color="#5cb85c">
            <CheckOutlined />
          </Tag>
        ) : (
          <Tag color="#cd201f">
            <CloseOutlined />
          </Tag>
        ),
    },
    {
      title: "Action",
      key: "id",
      width: 120,
      align: "center",
      fixed: "right",
      render: (item) => (
        <Row justify="center">
          <Col span={8}>
            <Button
              type="primary"
              style={{ backgroundColor: "#FAAD14" }}
              icon={<EditOutlined />}
              size="small"
              onClick={() => {
                handleEdit(item);
              }}
            />
          </Col>
          <Col span={8}>
            <Button
              onClick={() => {
                FRemove(
                  item.id,
                  `/api/setup/access-menu-permision`,
                  getListMenuUser
                );
              }}
              type="primary"
              icon={<DeleteOutlined />}
              size="small"
              danger
            />
          </Col>
        </Row>
      ),
    },
  ];
  const handleInsert = () => {
    setIsModal(!isModal);
  };

  const onViewAny = (value) => {
    setForms({
      ...forms,
      viewAny: value,
    });
  };
  const onView = (value) => {
    setForms({
      ...forms,
      view: value,
    });
  };
  const onUpdate = (value) => {
    setForms({
      ...forms,
      update: value,
    });
  };
  const onCreate = (value) => {
    setForms({
      ...forms,
      create: value,
    });
  };
  const onDelete = (value) => {
    setForms({
      ...forms,
      delete: value,
    });
  };

  const items = [
    {
      key: "1",
      label: (
        <div onClick={handleInsert}>
          <PlusOutlined /> Add Menu
        </div>
      ),
    },
  ];

  if (isLoading) return <CLoadingSpinner />;
  if (!accessList.viewAny) return <CError status={403} />;

  if (!isHaveData) {
    return <CError status={errors.status} />;
  }

  return (
    <>
      <CTitle />
      <Card
        cover={
          <Col
            span={24}
            style={{ backgroundColor: "#1890ff", paddingTop: "5px" }}
          />
        }
      >
        <Row>
          <Col span={22}>
            <CPageTitle title={detail.name} />
          </Col>
          <Col span={2}>
            <Button
              type="primary"
              onClick={() => router.push("/setup/access-menu")}
              style={{ float: "right" }}
            >
              Back
            </Button>
          </Col>
        </Row>
        <Row gutter={4} style={{ flex: 1 }}>
          <Col span={24}>
            <Tabs
              defaultActiveKey="1"
              items={[
                {
                  label: "Data",
                  key: "Data",
                  children: <CDetailUserGroupData data={detail} />,
                },
                {
                  label: "List Menu",
                  key: "ListMenu",
                  children: (
                    <CDetailUserGroupTable
                      items={items}
                      columns={columns}
                      data={listMenuUser}
                      isLoadingTable={isLoadingTable}
                      scroll={{ y: 500 }}
                    />
                  ),
                },
              ]}
            />
          </Col>
        </Row>
      </Card>

      <CModal
        id={forms.id}
        title="Menu"
        onCancel={onCancel}
        onSubmit={() =>
          FSubmit(
            "/api/setup/access-menu-permision",
            forms,
            setErrors,
            getListMenuUser,
            onCancel,
            setIsSubmit
          )
        }
        isSubmit={isSubmit}
        open={isModal}
      >
        <CSelect
          value={forms.menu_id}
          label="Menu"
          onChange={onMenu}
          options={listMenu}
          disabled={forms.id > 0 ? true : false}
        />
        <CSwitch
          value={forms.viewAny}
          onChange={onViewAny}
          label="View Any"
          name="viewAny"
        />
        <CSwitch
          value={forms.view}
          onChange={onView}
          label="View"
          name="view"
        />
        <CSwitch
          value={forms.create}
          onChange={onCreate}
          label="Create"
          name="create"
        />
        <CSwitch
          value={forms.update}
          onChange={onUpdate}
          label="Update"
          name="update"
        />
        <CSwitch
          value={forms.delete}
          onChange={onDelete}
          label="Delete"
          name="delete"
        />
      </CModal>
    </>
  );
};

export default DetailAccessMenu;
